import {
    PARIS_LONG,
    PARIS_LAT
} from './constants';

const ZOOM = 8;

const baseOptions = {
    center: {
        lat: PARIS_LAT, 
        lng: PARIS_LAT
    },
    zoom: ZOOM
}

function initMap() {
    const mapDiv = document.getElementById('map');
    const map = new google.maps.Map(mapDiv, baseOptions);

}